.nav {
  /*position: relative;*/
  grid-area: nav;
}

@media screen and (min-width: 1200px) {
  .nav {
    width: 768px;
    height: 56px;
    margin-right: 40px;
    padding-left: 129px;
    padding-right: 40px;
    border-right: 1px solid rgba(33, 43, 49, 0.2);
  }
}

.nav__menu {
  position: absolute;
  top: -75px;
  right: -5vw;
}

@media screen and (min-width: 1200px) {
  .nav__menu {
    padding: 0;
  }
}

.nav__mobile {
  position: absolute;
  top: 0px;
  right: 0px;
}