.contacts {
  display: flex;
  flex-wrap: wrap;
  background-color: #F7F9FA;
}

@media screen and (min-width: 992px) {
  .contacts {
    position: relative;
  }
}

.contacts__email-block {
  padding-top: 48px;
}

@media screen and (min-width: 992px) {
  .contacts__email-block {
    width: 45%;
  }
}

@media screen and (min-width: 1200px) {
  .contacts__email-block {
    max-width: 600px;
    padding-top: 56px;
  }
}

.contacts__title {
  margin-bottom: 16px;
  font-weight: bold;
  font-size: 24px;
  line-height: 31px;
}

@media screen and (min-width: 1200px) {
  .contacts__title {
    font-size: 32px;
  }
}

.contacts__text {
  display: block;
  margin-bottom: 40px;
  font-weight: 500;
  font-size: 18px;
  line-height: 17px;
}

.contacts__form {
  margin-bottom: 48px;
}

@media screen and (min-width: 1200px) {
  .contacts__button-block {
    margin-bottom: 56px;
  }
}

.contacts__map {
  width: 100%;
  height: 351px;
  background-image: url(./img/contacts-bg.jpg);
  background-repeat: no-repeat;
  background-position: 0 0;
  overflow: hidden;
}

@media (-webkit-min-device-pixel-ratio: 2),
(min-resolution: 2dppx) {
  .contacts__map {
    background-image: url("./img/contacts-bg@2x.jpg");
  }
}

